// 将时间毫秒数转换为日期格式
export function timeformer (val, min) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  let y = d.getFullYear()
  let m = ''
  let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  if (min === 'day') {
    m = d.getMonth() + 1
  } else {
    m = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1) // 获取当前月份的日期，不足10补0
  }
  let commonTime = ''
  if (min) {
    var hour = d.getHours()
    var minute = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
    // var second = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
    commonTime = y + '-' + m + '-' + day + ' ' +  hour + ':' + minute
  } else {
    commonTime = y + '-' + m + '-' + day
  }
  return commonTime
}
export function timeDate (val, min) {
  if (!val) return
  var d = new Date()
  d.setTime(val)
  let y = d.getFullYear()
  let m = d.getMonth() + 1 // 获取当前月份的日期，不足10补0
  let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
  let commonTime = y + '-' + m + '-' + day
  return commonTime
}
export function setSessionStorage (key, value, parseJSON = true) {
  console.log(`write sessionStorage "${key}"`)
  window.sessionStorage.setItem(key, parseJSON ? JSON.stringify(value) : value)
}
// 获取当前周的周一到周日
export function getDates(str) {
  var new_Date = ''
  if (str) {
    new_Date = new Date(str.replace(/-/g, '/'))
  } else {
    new_Date = new Date()
  }
  var timesStamp = new_Date.getTime();
  var currenDay = new_Date.getDay();
  var dates = [];
  for(var i = 0; i < 7; i++) {
    let d = new Date(timesStamp + 24 * 60 * 60 * 1000 * (i - (currenDay + 6) % 7))
    let y = d.getFullYear()
    let m = d.getMonth() + 1 // 获取当前月份的日期，不足10补0
    let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate() // 获取当前几号，不足10补0
    let commonTime = y + '-' + m + '-' + day
    dates.push(commonTime);
  }
  return dates
}
/**
   * @Author    wuhaiyan
   * @DateTime  2017-12-13
   * @param     {[strIng]}    key       [存储的键值]
   * @param     {[string]}    value     [默认为空]
   * 方便获取json对象
   */
export function getSessionStorage (key, defaultValue = '', parseJSON = true) {
  console.log(`read sessionStorage "${key}"`)
  let value = window.sessionStorage.getItem(key)
  if (value) {
    return parseJSON ? JSON.parse(value) : value
  }
  return defaultValue
}

export function isApiResponseOk (response) {
  return response && response.errorCode === 0
}
export function formatDate (date, fmt) {
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  }
  let o = {
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds()
  }
  for (let k in o) {
    if (new RegExp(`(${k})`).test(fmt)) {
      let str = o[k] + ''
      fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? str : padLeftZero(str))
    }
  }
  return fmt
}
export function padLeftZero (str) {
  return ('00' + str).substr(str.length)
}
// 根据日期返回前后日期
export function getStatDate (str) {
  let date = new Date()
  let date_end = new Date()
  //let y = date.getFullYear()
  //let m = date.getMonth() + 1 // 获取当前月份的日期，不足10补0
  //let day = date.getDate() < 10 ? '0' + date.getDate() : date.getDate() // 获取当前几号，不足10补0
  let begin = new Date(date.setDate(date.getDate() + parseInt(str))) // str天前的日期
  let end = new Date(date_end.setDate(date_end.getDate())) // 今天天的日期
  let time1 = begin.getFullYear() + ' /' +  (begin.getMonth() + 1)  + '/' + begin.getDate() + ' 00:00:00'
  let time2 = end.getFullYear() + '/' +  (end.getMonth() + 1) + '/' +  end.getDate() + ' 23:59:59'
  let beiginTime = new Date(time1).getTime()
  let endTime = new Date(time2).getTime()
  let data = {
    beiginTime: beiginTime,
    endTime: endTime
  }
  return data
}
//  判断当前使用的哪个浏览器
export function myBrowser(){
  var userAgent = navigator.userAgent; //取得浏览器的userAgent字符串
  var isOpera = userAgent.indexOf("Opera") > -1;
  if (isOpera) {
      return "Opera"
  }; //判断是否Opera浏览器
  if (userAgent.indexOf("Firefox") > -1) {
      return "FF";
  } //判断是否Firefox浏览器
  if (userAgent.indexOf("Chrome") > -1){
      return "Chrome";
  }
  if (userAgent.indexOf("Safari") > -1) {
      return "Safari";
  } //判断是否Safari浏览器
  if (userAgent.indexOf("compatible") > -1 && userAgent.indexOf("MSIE") > -1 && !isOpera) {
      return "IE";
  }; //判断是否IE浏览器
  if (userAgent.indexOf("Trident") > -1) {
      return "Edge";
  } //判断是否Edge浏览器
}
// 获取本月的日期
export function getMonthDate() {
  // 获取当前月的第一天  
  var start = new Date();  
  start.setDate(1);  
  // 获取当前月的最后一天  
  var date = new Date();  
  var currentMonth = date.getMonth();  
  var nextMonth = ++currentMonth;  
  var nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1);  
  var oneDay = 1000 * 60 * 60 * 24;  
  var end = new Date(nextMonthFirstDay - oneDay);  

  var startDate = new Date(transferDate(start) + ' 00:00:00').getTime(); // 日期变换 
  var cdate =  new Date(transferDate(end));
  var endDate = cdate.setDate(cdate.getDate() + 1);
  let data = {
    beiginTime: startDate ,
    endTime: endDate
  }

  return data; 
}

function transferDate(date) {  
  // 年  
  var year = date.getFullYear();  
  // 月  
  var month = date.getMonth() + 1;  
  // 日  
  var day = date.getDate();  
  if (month >= 1 && month <= 9) {  
      month = "0" + month;  
  }  
  if (day >= 0 && day <= 9) {  
      day = "0" + day;  
  }  
  var dateString = year + '/' + month + '/' + day;  
  return dateString;  
} 
export function transDate(str) {  
  // 年  
  var date = new Date(str)
  // var year = date.getFullYear();  
  // 月  
  var month = date.getMonth() + 1;  
  // 日  
  var day = date.getDate();  
  if (month >= 1 && month <= 9) {  
      month = "0" + month;  
  }  
  if (day >= 0 && day <= 9) {  
      day = "0" + day;  
  }  
  var dateString = month + '/' + day;  
  return dateString;  
}


